nav {
    width: 100%;
    background-color: rgb(58, 58, 58);
    display: flex;
    justify-content: space-around;
    align-items: center;
    .nav-links {
        list-style: none;
        display: flex;
        .nav-item a {
            display: inline-block;
            text-decoration: none;
            color: white;
            padding: 4em 0;
            
            &:hover {
                color: white;
            }
            &.active {
                font-weight: bold;
                color: rgb(0, 255, 191);
            }
        }
    }
}

nav.vertical {
    width: 120px;
    height: 100vh;
    flex-direction: row;
    position: absolute;
    left: 0%;
    top: 0;
    .nav-links {
        flex-direction: column;
        .nav-item a {
            display: block;
            padding: 4em 0;
        }
    }
}